@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;900&family=Ubuntu&display=swap);
body{background:#3aafa9;text-align:center;color:#17252a;font-family:"Montserrat";position:relative;min-height:100vh}body h1,body h2,body h3,body h4,body h5,body h6{color:#f5f5f5}
body input[type=text]{width:18rem;padding:12px 20px;margin:8px 8px;display:inline-block;border:1px solid #ccc;border-radius:4px;box-sizing:border-box}body button{width:10rem;background-color:#2b7a78;color:#fff;padding:14px 20px;margin:.2rem;border:none;border-radius:4px;cursor:pointer}body button:hover{background-color:#17252a}body button:focus{background-color:#17252a}body .history{background-color:#def2f1;border-radius:8px;text-align:left;width:25%}body ul{list-style-type:none}

ul{background-color:#def2f1;border-radius:8px;text-align:left;border:2px;width:20rem;margin-right:8px}
.results{background-color:#def2f1;border-radius:8px;text-align:left;width:30rem}
footer{position:absolute;bottom:0;width:100%;color:#17252a}
